/**
 * AUTHOR BOX.
 */

/**
 * Dependencies.
 */
import React from 'react'
import PropTypes from 'prop-types'
//import { LocalesContext } from '../components/locales/LocalesContextProvider'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFacebook, faTwitter, faLinkedin } from '@fortawesome/free-brands-svg-icons'

/**
 * FA ICON COMPONENTS.
 */
//const FaGoogleIcon = () => <FontAwesomeIcon icon={faGoogle} />
const FaFacebookIcon = () => <FontAwesomeIcon icon={faFacebook} />
const FaTwitterIcon = () => <FontAwesomeIcon icon={faTwitter} />
const FaLinkedInIcon = () => <FontAwesomeIcon icon={faLinkedin} />

/**
 * React component (JSX).
 */
const AuthorBox = ( {author} ) => {

  // Get localized data.
  //const { langKey, i18n } = useContext(LocalesContext)
  //const i18nData = i18n.find(el => el.langKey === langKey)
  //const { author } = i18nData.locales

  // Render component.
  return (
  <>
  <div className="authorBox">
    <img src={author.photo} alt={author.name} width="140" height="140" />
    <p>
      <span className="authorBoxName">{author.name}</span>
      <span className="authorBoxBio">{author.bio}</span>
      <span className="authorBoxLinks">
        {author.links?.facebook ? <a href={author.links.facebook} target="_blank" rel="noreferrer" aria-label="Facebook Author Link"><FaFacebookIcon /></a> : null}
        {author.links?.twitter ? <a href={author.links.twitter} target="_blank" rel="noreferrer" aria-label="Twitter Author Link"><FaTwitterIcon /></a> : null}
        {author.links?.linkedin ? <a href={author.links.linkedin} target="_blank" rel="noreferrer" aria-label="LinkedIn Author Link"><FaLinkedInIcon /></a> : null}
      </span>
    </p>
  </div>
  </>
  )
}

/**
 * Properties.
 */
AuthorBox.propTypes = {
  author: PropTypes.object.isRequired
}

export default AuthorBox
